@import './styles/variables.scss';
@import './styles/forms.scss';
@import './styles/media_queries.scss';


body,
html {
  margin: 0;

  #root {
    flex: 100%;
  }
}

.Navbar {
  display: flex;
  height: 3rem;
  justify-content: space-between;
  gap: 1rem;

  img {
    object-fit: contain;
    height: 100%;
    width: 100%;
    padding: .5rem 1rem;
  }

  .logout-container{
    display:flex;
    align-items: flex-end;
    padding:1em;
  }
  
}

.NavbarMenu {
  list-style: none;
  display: flex;
  justify-content: space-around;
  background-color: rgba(0, 0, 0, 0.76);
  color: white;
  margin-bottom: 0;
  padding: .3rem;

  a {
    color: white;
    text-decoration: none;
  }
}


.MainContainer {
  display: flex;
  flex-flow: row nowrap;
  min-height: 100vh;

  .Sidebar {
    display: flex;
    flex-flow: column nowrap;
    background-color: $secondary-color;
    padding: 1em 1em 2em 3em;
    flex: 2 0 320px;

    &--closed {
      background-color: $secondary-color;
      flex: 0 0 2em;
      padding:1em .1em 0 1em;
    }

    .fa-solid.fa-bug {
      display: flex;
      justify-content: flex-end;
    }

    .Title {
      display: flex;
      justify-content: center;
      margin-top: 0;
      padding-top: 1rem;
    }

    .Options {
      list-style: none;
      width: 100%;
      padding: 1rem;

      li {
        padding: .4rem;
      }
    }
  }

  .MainContent {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    flex: 2 1 100%;
    padding: 2rem;
  }
}

.Footer {
  background-color: $primaryLight-color;
  height: 5rem;
  margin-top: auto;
}


.TodoApp {
  .add-todo-form {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    gap: 1em;

    .description-container {
      display: flex;
      flex-flow: column nowrap;
      .fa-solid.fa-square-pen.fa-xl {
        color: rgb(241, 136, 136)
      }

      &__button {
        color: $primary-color;
      }

      &__input {
        width: 80%;
        font-size: 1.2em;
      }

      &__input:focus {
        outline: 3px solid $primaryLight-color;
        border: 1px solid $primary-color;
      }
    }

    .duedate-container {
      display: flex;
      flex-flow: column nowrap;

      &__input {
        width: 80%;
        font-size: 1.4em;
      }
      &__button {
        background-color: #04AA6D;
        color: white;
        width: 50%;
        margin: 0 auto;
        border-radius: 10px;
        height: 3em;
        font-size: 1em;
      }
    }

    .TodoList {
      list-style: none;
      display: flex;
      flex-flow: column wrap;
      padding: 0;

      .Todo {
        padding: .2rem 1rem;

        &--checked {
          text-decoration: line-through;
        }

        .fa-solid.fa-trash-can {
          color: rgb(109, 13, 13);

        }
      }
    }
  }
}

  /**
  Buttons
*/

  .signUp {
    position: relative;
    width: 93px;
    height: 40px;
    top: 50%;
    margin-top: -20px;
    border-radius: 25px;
    margin-right: -39px;
    font-size: 16px;
    line-height: 1.5;
    z-index: 5;
    background-color: $primary-color;
    color: #ffffff;
    border: none;

    &:hover {
      background-color: $primaryHover-color;
    }
  }

  .login {
    position: relative;
    width: 112px;
    height: 40px;
    top: 50%;
    margin-top: -20px;
    background-color: $primaryLight-color;
    border-radius: 25px;
    padding-left: 39px;
    font-size: 16px;
    line-height: 1.5;
    z-index: 4;
    border: none;

    &:hover {
      border: 2px solid $primary-color;
    }
  }

  .login-title {
    margin-top: 0;
  }


  .form-group {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    gap: 1rem;
    margin: 1rem;
    .input {
      font-size:1.2em;
    }
  }


  :disabled {
    opacity: 30%;
    pointer-events: none;
  }

  datalist {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    writing-mode: horizontal-lr;
    width: 100%;
  }

  option {
    padding: 0;
  }

  input[type="range"] {
    width: 100%;
    margin: 0;
  }


  .InputHeight input {
    width: 4rem;
  }